        Opal.def(self, '$getRoofBrakeTable', $Elysion_getRoofBrakeTable$29 = function $$getRoofBrakeTable() {
          var self = this, name = nil, table = nil;

          
          name = "屋上";
          table = ["風紀委員の巡回！\n「そこ、何やってる！」風紀委員に見つかった。現在が授業パートであれば、こっぴどく叱られる。〔政治力〕で判定を行う。失敗すると、次のパートは行動できない。授業パート以外であれば、心身ともにリフレッシュ。《アウル》が1点回復する。", "通り雨\n「キャ〜〜〜〜！」突然雨がふってきた！みんな屋上を去っていく。屋上にいるキャラクターの中から好きな者を一人選び、そのキャラクターの自分に対する《感情値》が1点上昇する（屋上に誰がいるのかは、最終的にＧＭが決定すること）。", "自動販売機\n「なんで屋上に自動販売機があるんだ？」細かい事は気にせず何かのもう。〔政治力〕の判定を行う。成功すると、アイテムの【ポーション】か【お酒】か【タバコ】のいずれか一つを獲得できる。", "青空\n空を見上げると、自分がちっぽけな存在に思えてくる。〔青春力〕で判定を行う。成功すると、使用回数に制限がある授業かコミュを一つ選ぶ。その使用回数が一度分回復する。", "物思い\n空をながめながら、物思いにふける。俺ってアイツの事、どう思ってるんだろう？〔青春力〕で判定を行う。成功すると、ＰＣの中から好きなキャラクター一人を選ぶ。そのキャラクターに対する、《感情値》が1点上昇する。", "開放感\n開放的で非常に気分がいい。《アウル》が1点と「眠気」の変調が回復する。", "学園は広大だわ\n街並みを見下ろす。一体、この学園で何が起きているんだろう？〔学力〕で判定を行う。成功すると手がかり一つを選び、その情報を公開する。失敗すると、ほかの学生たちの姿が目に映り、今の自分に疑問がわいてくる。《アウル》が1点減少する。", "嵐の予感\n雲の動きが早くなっている。吹き荒ぶ風に、嵐の予感を感じた。〔青春力〕で判定を行う。成功すると、そのセッションの間、《アウル》の限界値が1点上昇する。失敗すると風に吹かれて風邪をひいてしまう。「病気」の変調を受ける。", "昼寝屋\nハンモックを貸し出している「昼寝屋」を発見。気持ちよさそうだ。〔政治力〕で判定を行う。成功すると、《生命力》が2D6点と「眠気」の変調が回復する。", "欲望の宴\n美味しそうな弁当を食べる者、恋人同士でイチャイチャするもの、怠惰な眠りを貪る者……屋上は欲望の見本市のようだ。「空腹」、「孤独」、「眠気」の内、好きな変調を受けることができる。受けた変調一種につき《アウル》が2点回復する。", "サビシガリヤ\n「……あなたも一人？」もしも「孤独」の変調を受けていたら、寂しそうな異性に声をかけられる。好きな異性のキャラクター一人を選ぶ。自分とそのキャラクターは、お互いに対する《感情値》が2点上昇する。そして好きな変調一つが回復する。"];
          return self.$getBreakTable(name, table);
        }, $Elysion_getRoofBrakeTable$29.$$arity = 0);
        
